#loan {

    #loan__select {
        cursor: pointer;
        margin: 10rem 0 6rem 0;

        #loan__viewer {
            display: flex;
            align-items: center;
            justify-content: space-between;

            padding: 0 4rem;
            height: 12.6rem;

            border-radius: 1rem;
            box-shadow: 0 0.3rem 1rem 0 hsla(0, 0%, 0%, 0.103);

            span {
                @include text-effect( $medium-font, medium-font, lato-font, 500, var( --second-text-color ) );
            }

            img {
                width: 3.9rem;
                height: 2.2rem;
                transition: all .6s;
            }
        }

        #loan__parcels {
            transition: all .3s;
            overflow: auto;
            margin-top: 2rem;

            max-height: 40rem;
            box-shadow: 0 0.3rem 1rem 0 hsla(0, 0%, 0%, 0.103);
            
            .loan__parcel {
                position: relative;
                padding: 3.3rem 4rem;
                list-style: none;
                @include text-effect( $medium-font,  $medium-font,  $lato-font, 500, var( --second-text-color ) );

                &::after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    z-index: 3;
                    display: block;
                    
                    width: 100%;
                    max-width: 117rem;
                    height: 0.2rem;
                    background-color: var( --fourth-text-color );
                }
            }
        }
    }

    #loan__info {
        text-align: center;
        margin-top: 4rem;

        span {
            @include text-effect( $medium-font,  $medium-font,  $lato-font, 500, var( --second-text-color ) );
        }
        
        h2 {
            @include text-effect( $very-big-font,  $very-big-font,  $nunito-font, 600, var( --main-text-color ) );
            margin: 2rem 0;
        }

        button {
            transition: all .4s;
            cursor: pointer;
            display: block;
            @include text-effect( $very-big-font,  $very-big-font,  $nunito-font, 600, white );

            margin: auto;
            padding: 2rem 7rem;
            background: linear-gradient( to bottom, var( --main-color ), var( --light-main-color ) );

            border: none;
            border-radius: 1rem;
            margin-top: 2rem;

            &:hover {
                transform: scale(1.02);
            }
        }
    }
}

@media (max-width: 400px) {
    #loan {
        #loan__select {
            #loan__viewer {
    
                span {
                    font-size: 18px;
                }
            }
        }
    }
}
